<script>
  import ClickOutside from "svelte-click-outside";
  let tabs = [
    {
      name: "File",
      list: [
        {
          name: "New Project",
          action: () => {
            console.log("Hello WOrld!");
          }
        },
        {
          name: "Open Project",
          action: () => {
            console.log("Hello WOrld!");
          }
        },
        {
          name: "Save Project"
        },
        {
          name: "Import File"
        }
      ]
    },
    {
      name: "Edit",
      list: [
        {
          name: "Undo"
        },
        {
          name: "Redo"
        },
        {
          name: "Cut"
        },
        {
          name: "Copy"
        },
        {
          name: "Paste"
        }
      ]
    },
    {
      name: "View",
      list: [
        {
          name: "Color Palette"
        },
        {
          name: "Toolbox"
        }
      ]
    }
  ];

  let current = tabs[0].list[0].name;
</script>

<style lang="scss">.toolbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 30px;
  z-index: 1000;
  background-color: #594ffa;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.tabs {
  margin-left: 10px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center; }

.tab {
  height: 100%;
  padding: 0 10px;
  margin: 0 5px;
  background: none;
  border: none;
  border-radius: 0;
  color: white; }

.tab p {
  padding: 0;
  margin: 0;
  font-size: 14px; }

.tab:hover {
  background-color: #3f51b5; }

.list {
  padding: 5px 0 0 0;
  width: 200px;
  position: absolute;
  margin-top: 6px;
  display: none;
  background-color: #594ffa;
  color: white;
  text-align: left; }

.tab .list.expand {
  display: block; }

.list-item {
  width: 100%;
  padding: 0 5px;
  height: 25px;
  margin: 3px 0;
  border: none;
  border-radius: 0;
  background: none;
  color: white;
  border-bottom: 1px solid white;
  display: flex; }
  .list-item * {
    margin: auto 0; }

.list-item:hover {
  background-color: #3f51b5; }

/*# sourceMappingURL=toolbar.svelte.css.map */</style>

<ClickOutside
  on:clickoutside={() => {
    current = '';
  }}>
  <div class="toolbar">
    <div class="tabs">
      {#each tabs as tab}
        <button
          class="tab"
          on:click={() => {
            if (current == tab.name) {
              current = '';
            } else {
              current = tab.name;
            }
          }}
          on:mouseover={() => {
            if (current) {
              current = tab.name;
            }
          }}>
          <p>{tab.name}</p>
          <div class={current === tab.name ? 'list expand' : 'list'}>
            {#each tab.list as li}
              <button class="list-item" on:click={li.action}>
                <p>{li.name}</p>
              </button>
            {/each}
          </div>
        </button>
      {/each}
    </div>
  </div>
</ClickOutside>
